@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@600;700&family=Poppins:wght@300;400;500;600;700&display=swap');

html,body,#root {
  width: 100%;
  height: 100%;
  margin: 0;
  font-family: 'Inter', sans-serif;
}

ul, li{
  margin: 0;
  padding: 0;
  list-style: none;
}

a{
 text-decoration: none;
}

span, label {
  display: block;
}

input{
  padding: 0;
  margin: 0;
}
