.App {
  width: 100%;
  height: 100%;
  min-width: 1280px;
  text-align: center;
  position: relative;
  overflow: hidden;
}

.App-setting {
  background-color: #121212;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
  position: relative;
}

.App-setting > div:nth-of-type(1){
  position: absolute;
  margin-top:-50%;
  margin-left:-80%;
  width: 606px;
  height: 606px;
  border-radius: 723.056px;
  opacity: 0.8;
  background: linear-gradient(180deg, rgba(0, 194, 255, 0.00) 0%, #FF29C3 100%);
  filter: blur(100px);
}

.App-setting > div:nth-of-type(2){
  position: absolute;
  margin-top:60%;
  margin-left:100%;
  width: 606px;
  height: 606px;
  flex-shrink: 0;
  border-radius: 606px;
  opacity: 0.6;
  background: linear-gradient(180deg, rgba(0, 194, 255, 0.00) 0%, #FF29C3 100%);
  filter: blur(100px);
}

.App-setting2{
  position: absolute;
  width: 87%;
  height: 80%;
  border: 2px solid #ffffff18;
  border-radius: 24px;
  background: linear-gradient(107deg, rgba(255, 255, 255, 0.05) 3.44%, rgba(255, 255, 255, 0.02) 64.82%);
  backdrop-filter: blur(15px);
  display: flex;
  justify-content: center;
  box-sizing: content-box;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.5);
  overflow: hidden;
}

.App-setting2 > div{
  width: 100%;
  max-width: 1080px;
  height: 100%;
}

.Model_css{
  position: absolute;
  top:1%;
  right:8%;
}

.pillblue{
  width: 125px;
  height: 250px;
  position: absolute;
  top:50%;
  left:0%;
}

.pillblue > img{
  max-width: 100%;
  height: auto;
}
.css_nav{
  width: 100%;
  height: 70px;
  display: flex;
}

.css_nav > div{
  text-align: left;
  width: 200px;
  font-family: Poppins;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 70px;
}

.css_nav_ul{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 16px;
  font-family: Poppins;
  font-style: normal;
  font-weight: 400;
  position: relative;
}

.css_nav_ul > li{
  width: auto;
  margin: 30px;
}

.css_nav_ul > li > a{
  padding: 5px;
  box-sizing: border-box;
  color:white;
}

.css_pages{
  width: 100%;
  height: calc( 100% - 70px );
}

.slider_line{
  position: absolute;
  width: auto;
  left:171px;
  top:48px;
  height: 1px;
  background-color: rgb(255, 255, 255);
  transition: .4s ease-out;
}

.superToriod{
  position: absolute;
  left:100%;
  margin-left: -173px;
  margin-top: -220px;
  top: 100%;
}

.roundCube {
  position: absolute;
  margin-top: -130px;
  margin-left: 250px;
  animation: float 3s ease-in-out infinite; /* 둥둥 띄우는 애니메이션 설정 */
}

@keyframes float {
  0% {
    transform: translate(0, 0);
  }
  50% {
    transform: translate(0, -20px); /* 원하는 높이로 조정하여 이미지가 둥둥 띄워지는 정도를 설정 */
  }
  100% {
    transform: translate(0, 0);
  }
}